import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatConfirmDialogComponent } from './mat-confirm-dialog/mat-confirm-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  constructor(private dialog:MatDialog) { }

  OpenConfirmDialog(msg){
    return this.dialog.open(MatConfirmDialogComponent,{
       width:'390px',
       panelClass:'confirm-dialog-container', 
     //Panel class is used to allow our customize dialog box to apply css & make it as custom
       disableClose:true, // same like ignoreBackDropClick used in popUp in updateEmp
       position:{top:"35vh"}, //vh=> vertical height, 1vh=> 16px
       data:{
        message:msg
       }
     })
     }

}
