import { Component, OnInit, TemplateRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { HttpService } from '../http.service';
import { Employee } from '../model/employee';

@Component({
  selector: 'app-employee-details',
  templateUrl: './employee-details.component.html',
  styleUrls: ['./employee-details.component.css']
})
export class EmployeeDetailsComponent implements OnInit {

  empobj=<Employee>{};
  modalRef:BsModalRef;

  constructor(private route:ActivatedRoute,
              private http:HttpService,
              private title:Title,
              private modalservice:BsModalService) { }

  ngOnInit() {
    this.GetDataFromUrl();
    this.title.setTitle("Admin | Employee Details");
  }

  GetDataFromUrl(){
    this.route.paramMap
    .subscribe((param)=>{
      //console.log(param.get("id"));
      this.GetDataFromBackend(param.get("id"));
    })
  }

  GetDataFromBackend(id){
    this.http.getEmployeeDataById(id)
    .subscribe((response)=>{
      //console.log(response);
      this.empobj=(<Employee>response);
    })
  }

  onUpdate(popup: TemplateRef<any>){  // TemplateRef<any>=> angular 10 or 11 onwards requires to explicitely write this. otherwise in angular 8 it's optional.
    
      this.modalRef = this.modalservice.show(popup);
    
  }
}
