import { Component, OnInit, TemplateRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { HttpService } from '../http.service';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Employee } from '../model/employee';
import { DialogService } from '../shared/dialog.service';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  //empData: any[]=[];
  empData: Observable<Employee[]>;  // used for async pipe
  empObj=<Employee>{};
  isRadioSelect:boolean=false;
  modalRef:BsModalRef;
  p:number=1;
  nameSearch:string='';

  myconfig = {
    animated: true,  // to apply smooth animation to popup
    ignoreBackdropClick: true, // if we click outside of popup then popup should not disappear, so ignore click
    class: "alert alert-danger"
  }

  constructor(private service:HttpService,
              private title:Title,
              private modalservice:BsModalService,
              private dialogService:DialogService,
              private toastr:ToastrService) { }

  ngOnInit() {
    this.getAllEmpData();
    this.title.setTitle("Admin | Dashboard");
  }

  getAllEmpData(){
    /* this.service.getAllEmployeeData()
    .subscribe((response)=>{
      //console.log(response);
      this.empData=(<any>response);
    }) */

    this.empData=this.service.getAllEmployeeData();
  }

  onEdit(item){
    //console.log(item);
    this.isRadioSelect=true;
    this.empObj=item;
  }

  onCancel(){
    this.modalRef.hide();
    this.getAllEmpData();
  }

  onUpdate(popup: TemplateRef<any>){  // TemplateRef<any>=> angular 10 or 11 onwards requires to explicitely write this. otherwise in angular 8 it's optional.
    if(this.isRadioSelect){
      this.modalRef = this.modalservice.show(        
        popup, this.myconfig);
    }
    else{
      //alert("Please select a record to update...");
      this.toastr.warning("Please select a record to update...");
    }
  }

  onDelete(){
    if(this.isRadioSelect){
      this.dialogService.OpenConfirmDialog("Are you sure to delete this record?")
      .afterClosed()
      .subscribe((myRes)=>{
        if(myRes){
          this.service.deleteEmployee(this.empObj.empid)
          .subscribe((myResponse)=>{
            // console.log(myResponse);
            this.toastr.success(myResponse,"Record Deleted Successfully");
            this.getAllEmpData();
          })
        }
        else{
          //Don't delete
        }
      })
    }
    else{
      alert("Please select a record to delete...");
    }
  }

  onChangeStatus(){
    if(this.isRadioSelect){
      if(this.empObj.status=='suspended'){
        this.toastr.error("Employee already suspended, you can not change the status...");
      }
      else{
        this.service.changeStatus(this.empObj.empid)
        .subscribe((myResponse)=>{
          //console.log(myResponse);
          this.toastr.success(myResponse,"Employee Status changed...")
          this.getAllEmpData();
        })
      }
    }
    else{
      this.toastr.error("Please select a record to change the status...");
    }
  }
}
